import React, {useEffect, useState} from "react";
import {Button, Col, Form, Row} from "react-bootstrap";
import {combinations, employees as e, records, types2} from "../../helpers/data";
import Select from 'react-select'
import {
  getEmployeesNames,
  isValidCombination,
  prepareCombinations,
  resolveCombinations,
  setOf
} from "../../helpers/functions";

const Filter = ({showSM, setShowSM, cs, e, found, handleSearch}) => {

  const [filter, setFilter] = useState({
    // type: [], record: [],
    branches: [], cities: [],
    departments: [], division: [],
    // employeeName: '', superiorName: '',
    // recordName: ''
  });

  const [select, setSelect] = useState()
  const [toggle, setToggle] = useState([false])

  const [branches, setbranches] = useState(setOf(cs.map(c => c.branches)));
  const [divisions, setDivisions] = useState(setOf(cs.map(c => c.division)));
  const [departments, setdepartments] = useState(setOf(cs.map(c => c.departments)));
  const [cities, setCities] = useState(setOf(cs.map(c => c.cities)));

  const handleSelect = (data, event) => {
    const field = event.name
    setFilter({...filter, [field]: data})
    if (field !== 'removedEmployees'){
      commitChanges(field)
    }
  }

  const commitChanges = (select) => {
    setSelect(select)
    setToggle([!toggle[0]])
  }

  useEffect(() => {
    if (!isValidCombination(filter)) {
      setSelect('reset')
    }
    updateDropBox()
  }, [toggle]);

  const updateDropBox = () => {
    let update = cs

    // start of inner functions
    function updateField(field) {
      const values = filter[field].map(d => d.value)
      if (values.length) {
        update = update.filter(c => values.includes(c[field].value))
      }
      return values.length
    }

    const getSetOf = field => setOf(update.map(c => c[field]))

    function setbranches() {setbranches(getSetOf('branches'))}
    function setDivision() {setDivisions(getSetOf('division'))}
    function setdepartments() {setdepartments(getSetOf('departments'))}
    function setcities() {setCities(getSetOf('cities'))}
    // end of inner functions

    if (!updateField('branches')) setbranches()
    if (!updateField('division')) setDivision()
    if (!updateField('departments')) setdepartments()
    if (!updateField('cities')) setcities()

    if (select !== 'branches') setbranches()
    if (select !== 'division') setDivision()
    if (select !== 'departments') setdepartments()
    if (select !== 'cities') setcities()
  }

  const search = () => {
    console.log(filter)
    const res = {
      branches: filter.branches.map(v => v.value).join(','),
      cities: filter.cities.map(v => v.value).join(','),
      departments: filter.departments.map(v => v.value).join(','),
      division: filter.division.map(v => v.value).join(',')
    }
    // const r = resolveCombinations([filter])
    handleSearch(res)
  }

  const selector = (name, label, options) => {
    return (
      <Col>
        <Select
          isMulti={true}
          name={name}
          placeholder={label}
          options={options}
          onChange={(data, e) => handleSelect(data, e)}
        />
      </Col>
    )
  }

  return (
    <Form className="pb-4">
      <Row className='pb-2'>
        { selector("type", "Types", types2) }
        { selector("branches", "branches", branches) }
        { selector("division", "Divisions", divisions) }
        { selector("departments","departments", departments) }
        { selector("cities","Cities", cities) }
        { selector("record","Record", records) }
      </Row>
      <Row>
        { selector("employeeName", "Employee Name", e) }
        { selector("superiorName", "Superior Name", e) }

        {/* BUTTONS */}
        <Col className="text-right">
          <Button className="mr-1" onClick={search}>
            Search
          </Button>
          <Button className="mr-1"
            disabled={!found.length}
            onClick={() => setShowSM(!showSM)}
          >
            {`Show ${showSM ? 'table' : 'skillMatrix'}`}
          </Button>
          <Button disabled={!found.length}>
            Export
          </Button>
        </Col>
      </Row>
    </Form>
  )
}

export default Filter;

